





















import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import VueRouter from 'vue-router'
import page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
import { deepClone } from '@/until/index'
@Component({
  components: {
    page,
    pageView
  }
})

export default class noticeConfig extends Vue {
  public $router: VueRouter | undefined
  public $route: any
  private className: string = '使用线路'

  private showBtn = false;

  private coordinate = []


  private hidePopup (refId) {
    const component: any = this.$refs[refId]
    component.hide()
  }


  private saveSchoolInfo () {
    let backData = this.$store.state.backData;
    let that = this;
    // if (!backData) {
    //   const toast = (this as any).$createToast({
    //     txt: '请选择线路',
    //     type: 'correct'
    //   })
    //   toast.show()
    //   return
    // }
    that.$store.commit('SET_BACK_DATA', {
      route: 'lineConfig',
      data: {
        selectList: that.mateList,
        backInfo: that.backInfo
      }
    })
    this.$router.back();
  }

  backInfo = {}
  mateList = []

  options = []

  mounted () {
    let that = this
    let query = this.$route.query
    this.backInfo = query.backInfo
    console.log(query, 'bbbbbbbbbbbbbbbbbbbbbbbbbbbbbb')
    this.mateList = query.mateList || []
    this.fleetId = query.fleetId
    this.getLineOptions(this.fleetId, query.updateType)
  }

  getLineOptions(fleetId, type) {
    const toast = this['$createToast']({
      time: 0,
      txt: '获取中...'
    })
    toast.show()
    this.$store.dispatch('getLineByFleetId', { id: fleetId, type: type}).then((response) => {
      if (response.success) {
        console.log(response, 'oooooooooo')
        this.options = response.data || []
      }  
      toast.hide()
    })
  }

  showToastType () {
    const toast = (this as any).$createToast({
      txt: '保存成功',
      type: 'correct'
    })
    toast.show()
  }

}
